.videoFooter {
  position: relative;
  bottom: 25%;
  color: white;
  margin-left: 30px;
}

.videoFooter__music {
  display: flex;
  align-items: center;
}

.videoFooterMusic__text {
  width: 80%;
  overflow-x: hidden;
}

.videoFooterMusic__text p {
  animation: moveText 5s infinite linear;
}

.videoFooter__record {
  animation: spinTheRecord 5s infinite linear;
  position: absolute;
  bottom: 3px;
  right: 20px;
  height: 50px;
  filter: invert(1);
}

@keyframes spinTheRecord {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes moveText {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}

@media only screen and (max-width: 440px){
  .videoFooter{
    bottom: 22%;
  }
  .videoFooterMusic__text{
    width: 78%;
  }
}